.library-body{
    height: 87%;
    width: 92%;
    padding: 3%;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    justify-content: space-between;
    /* border: 2px solid white; */
}

.playlist-card{
    position: relative;
    width: 15%;
    height: 40%;
    border-radius: 20px;
    background-color: rbg(30,42,62);
    border: 1px solid rgba(54,69,98,0.18);
    padding: 1%;
    margin-bottom: 2%;
    background:linear-gradient(75deg,rgb(40,58,88)0%,rgba(54,69,98,0)100%);
    transition: all 0.2s ease;
    /* display: flex;
    flex-direction: column; */
}
.playlist-image{
    /* width: 100%; */
    /* height: 100%; */
    width: 160px;
    height: 140px;
    border-radius: 20px;
    aspect-ratio: 1;
}

.playlist-card:hover{
    transform:scale(1.05);
}
.playlist-card:hover .playlist-fade{
    opacity: 1;
}
.playlist-title{
    font-weight: 800;
    font-size: 16px;
    color: #c4d0e3;
    margin: 10px 0px 10px;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
.playlist-subtitle{
    font-weight: 400;
    font-size: 12px;
    margin: 0px;
    color: #c4d0e37c;
}
.playlist-fade{
    position: absolute;
    right: 0;
    bottom: 0;
    opacity: 0;
    width: 90%;
    height: 34%;
    border-radius: 20px;
    background: linear-gradient(180deg,rgba(54,69,98,0)10%,rgba(54,69,98,1)100%);
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 5%;
    transition: 0.5s ease;
    /* border: 2px solid white; */
}